    -ms-flex-pack: start;
    justify-content: flex-start;
    text-align: start;
  }
  .center-lg {
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    text-align: center;
  }
  .end-lg {
    -webkit-box-pack: end;
    -ms-flex-pack: end;
    justify-content: flex-end;
    text-align: end;
  }
  .top-lg {
    -webkit-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
  }
  .middle-lg {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
  }
  .bottom-lg {
    -webkit-box-align: end;
    -ms-flex-align: end;
    align-items: flex-end;
  }
  .around-lg {
    -ms-flex-pack: distribute;
    justify-content: space-around;
  }
  .between-lg {
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
  }
  .first-lg {
    -webkit-box-ordinal-group: 0;
    -ms-flex-order: -1;
    order: -1;
  }
  .last-lg {
    -webkit-box-ordinal-group: 2;
    -ms-flex-order: 1;
    order: 1;
  }
}

.intro {
  background-image: url("../img/bg-intro.jpg");
  background-size: cover;
  height: 560px;
  position: relative;
  overflow: hidden;
}

.intro__text {
  color: #ffffff;
  position: absolute;
  bottom: 70px;
}

.intro__text p {
  font-size: 26px;
}

.intro__text h2 {
  font-size: 55.5px;
  line-height: 1.16;
  position: relative;
}

.intro__text h2:before {
  content: '';
  background-image: url("../img/sprite.png");
  width: 243px;
  height: 159px;
  background-position: 0px 0px;
  display: block;
  position: absolute;
  right: -210px;
  top: -150px;
}

.intro__form {
  background: #ffffff;
  padding: 45px 60px;
  margin-top: 50px;
}

.intro__form form h3 {
  font-size: 32px;
  margin-bottom: 35px;
  color: #3f3f3f;
}

.intro__form form p {
  margin-top: 30px;
  font-size: 18.5px;
  color: #212121;
}

.header {
  padding-top: 70px;
  padding-bottom: 30px;
}

.header .logo {
  display: flex;
  justify-content: center;
  align-items: center;
}

.header .logo img {
  width: 230px;
}

.header__info {
  display: flex;
  flex-wrap: wrap;
}

.header__info > p {
  margin-bottom: 25px;
  font-size: 22px;
  width: 100%;
  color: #212121;
}

.header__info__block:nth-of-type(1) {
  width: 40%;
}

.header__info__block:last-child {
  width: 60%;
}

.header__info__block a {
  font-size: 22px;
  display: block;
  text-decoration: none;
  color: #000;
}

.header__info__block p {
  font-size: 22px;
}

.header .btn:first-child {
  margin-right: 15px;
}

.header__sections {
  margin-top: 65px;
}

.header__sections ul {
  display: flex;
  justify-content: space-between;
}

.header__sections ul li {
  height: 100px;
  background-color: #f2f3f9;
  width: 100%;
}

.header__sections ul li:not(:last-child) {
  margin-right: 8px;
}

.header__sections ul li .icon-section-1 {
  background-image: url("../img/sprite.png");
  width: 43px;
  height: 46px;
  background-position: -253px -57px;
  margin-right: 25px;
}

.header__sections ul li .icon-section-2 {
  background-image: url("../img/sprite.png");
  width: 49px;
  height: 44px;
  background-position: 0px -169px;
  margin-right: 25px;
}

.header__sections ul li .icon-section-3 {
  background-image: url("../img/sprite.png");
  width: 41px;
  height: 47px;
  background-position: -253px -113px;
  margin-right: 25px;
}

.header__sections ul li .icon-section-4 {
  background-image: url("../img/sprite.png");
  width: 45px;
  height: 47px;
  background-position: -253px 0px;
  margin-right: 25px;
}

.header__sections ul li .icon-section-5 {
  background-image: url("../img/sprite.png");
  width: 38px;
  height: 45px;
  background-position: 0px -223px;
  margin-right: 25px;
}

.header__sections ul li a {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 100%;
  padding-left: 25px;
  font-size: 22px;
  color: #b8b9bd;
  text-decoration: none;
  flex-direction: row-reverse;
  transition: all 0.3s;
}

.header__sections ul li a:hover, .header__sections ul li a.active {
  background: #ffffff;
  color: #212121;
}

.header__btn {
  display: flex;
}

.container-fluid {
  padding: 0 65px;
}

body {
  font-family: 'Ubuntu', sans-serif;
}

.roboto, .intro__form form p, .header__info > p, form input:not(:last-child) {
  font-family: 'Roboto', sans-serif;
}

.ubuntu, .header__info__block p, .btn {
  font-family: 'Ubuntu', sans-serif;
}

.btn {
  border-radius: 4px;
  color: #ffffff;
  text-transform: uppercase;
  font-size: 19.5px;
  padding: 20px 40px;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s;
}

.btn span, .btn i {
  z-index: 100;
  position: relative;
}

.btn i {
  font-size: 28px;
  margin-right: 30px;
}

.btn-primary {
  background-color: #2849ae;
  position: relative;
}

.btn-primary:before {
  content: '';
  background-image: linear-gradient(94deg, #2335a7, #4ae0e2 97%);
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  transition: all 0.3s;
}

.btn-primary:hover:before {
  opacity: 1;
}

form input {
  background: none;
  border: 1px solid #b2b2b2;
  border-radius: 6px;
  display: block;
  width: 100%;
  box-sizing: border-box;
}

form input:not(:last-child) {
  margin-bottom: 20px;
  padding: 28px 20px;
  font-size: 18.5px;
}

form button {
  width: 100%;
}
