form {
  input {
    background: none;
    border: 1px solid #b2b2b2;
    border-radius: 6px;
    display: block;
    width: 100%;
    box-sizing: border-box;
    &:not(:last-child) {
      margin-bottom: 20px;
      padding: 28px 20px;
      font-size: 18.5px;
      @extend .roboto; } }
  button {
    width: 100%; } }
