$icons: (0:0);
$icons: map-merge($icons,('arrow': (X: 0px, Y:0px, W: 243px, H: 159px, TW: 298px, TH: 268px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('facebook': (X: -283px, Y:-170px, W: 10px, H: 19px, TW: 298px, TH: 268px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('facebook@2x': (X: -253px, Y:-170px, W: 20px, H: 38px, TW: 298px, TH: 268px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('section-1': (X: -253px, Y:-57px, W: 43px, H: 46px, TW: 298px, TH: 268px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('section-2': (X: 0px, Y:-169px, W: 49px, H: 44px, TW: 298px, TH: 268px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('section-3': (X: -253px, Y:-113px, W: 41px, H: 47px, TW: 298px, TH: 268px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('section-4': (X: -253px, Y:0px, W: 45px, H: 47px, TW: 298px, TH: 268px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('section-5': (X: 0px, Y:-223px, W: 38px, H: 45px, TW: 298px, TH: 268px, IMG: '../img/sprite.png')));


// Gets an attribute from the sass map
@function icon-attr($icon, $attr) {
  $icon: map-get($icons, $icon);
  @return map-get($icon, $attr); }

@mixin sprite($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  width: icon-attr($iconName, W);
  height: icon-attr($iconName, H);
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-position($iconName) {
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-retina($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  $width: icon-attr($iconName, W);
  $height: icon-attr($iconName, H);
  width: $width/2;
  height: $height/2;
  $x: icon-attr($iconName, X);
  $y: icon-attr($iconName, Y);
  background-position: $x/2 $y/2;
  $tw: icon-attr($iconName, TW);
  $th: icon-attr($iconName, TH);
  background-size: $tw/2 $th/2; }


@mixin s($i) {
  @include sprite($i); }
@mixin sp($i) {
  @include sprite-position($i); }
@mixin sr($i) {
  @include sprite-retina($i); }

