.btn {
  border-radius: 4px;
  color: $white;
  text-transform: uppercase;
  font-size: 19.5px;
  padding: 20px 40px;
  text-decoration: none;
  @extend .ubuntu;
  @include center;
  @include tr;
  span, i {
    z-index: 100;
    position: relative; }
  i {
    font-size: 28px;
    margin-right: 30px; }
  &-primary {
    background-color: $blue;
    position: relative;
    &:before {
      content: '';
      background-image: linear-gradient(94deg, $blue2, $aqua 97%);
      width: 100%;
      height: 100%;
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      @include tr; }
    &:hover:before {
      opacity: 1; } } }

